#footer {
  min-height: 360px;
}
#footer-header {
  min-height: 60px;
  background: #1d222c;
  border-top: solid 1px #202a33;
  border-bottom: solid 1px #202a33;
  padding-top: 20px;
}
#footer-content {
  min-height: 200px;
  background: #1d222c;
  border-bottom: solid 1px #202a33;
  padding-top: 30px;
}
#footer h1,
#footer h2,
#footer h3,
#footer h4,
#footer h5 {
  line-height: 1;
}
#footer a {
  color: #00aeff;
}
#footer a:hover {
  color: #3dc1ff;
}
#footer ul,
#footer ol {
  margin-top: 0;
  margin-bottom: 10px;
  padding: 0px;
}
#footer li {
  margin-top: 5px;
  list-style: none;
  display: list-item;
  text-align: -webkit-match-parent;
}
#footer-content .footer-content-block h4 {
  font-weight: 300;
  margin-bottom: 20px;
  color: #00aeff;
}
#footer-content .footer-content-block {
  color: #7B88A0;
}
#footer-content .footer-content-block p a.highlight {
  color: #00aeff;
}
#footer-footer {
  min-height: 100px;
  padding-top: 40px;
  background: #171b23;
  color: #7B88A0;
}
#footer-footer .footer-copyright {
  float: left;
}
#footer-footer .footer-social-media {
  float: right;
}
#footer-footer .footer-social-media li {
  display: inline-block;
  margin-top: 0px;
  margin-right: 5px;
}
/* Old Footer */
.footer {
  background: #010E16;
  border: 1px solid #305f79;
  border-radius: 0px;
  padding: 10px;
  margin-bottom: 10px;
}
.footer p {
  margin: 0px;
}
.footer p.links a {
  margin-right: 10px;
}
.copyright {
  font-size: 12px;
  margin-top: 10px;
  display: block;
}
.copyright p {
  padding: 0px;
  margin-bottom: 5px;
}
