.NewsTile-content {
  padding-top: 50%;
  width: 100%;
  margin-bottom: 30px;
  background-position: center center;
  background-size: cover;
  position: relative;
}
.NewsTile a {
  display: block;
  height: 100%;
  width: 100%;
}
.NewsTile-description {
  padding: 10px;
  background: rgba(0, 0, 0, 0.7);
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
.NewsTile-description p {
  margin: 0px;
}
